<template>
    <button @click="up"></button>
</template>

<script>
  export default {
    name: 'Up.vue',
    methods:{
      up:function () {
        this.$http.post("/console/update").then(function(){
          this.$Message.success('更新成功')
        },function(){
          this.$Message.warning('更新失败')
        })
      }
    }
  }
</script>

